import Axios from "./axios"  // 导入配置好的axios文件
import qs from  "qs"

export function queryPayLogPageList(datas) {
  return Axios({
    url: "/paylog/queryPayLogPageList",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //设置请求头请求格式form
    },
    data: qs.stringify(datas)
  })
}

export function changePayLogStatus(datas) {
  return Axios({
    url: "/paylog/changePayLogStatus",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //设置请求头请求格式form
    },
    data: qs.stringify(datas)
  })
}

export function sendNotifyMsg(datas) {
  return Axios({
    url: "/paylog/sendNotifyMsg",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //设置请求头请求格式form
    },
    data: qs.stringify(datas)
  })
}




