import Axios from "./axios"  // 导入配置好的axios文件

export function getMenuList(datas) {
    return Axios({
        url: "/permission/getMenuList",
        method: "post",
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded' //设置请求头请求格式form
        },
        data: datas
    })
}





