import Axios from "./axios"  // 导入配置好的axios文件
import qs from  "qs"

export function queryOperateLogPageList(datas) {
  return Axios({
    url: "/operatelog/queryOperateLogPageList",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //设置请求头请求格式form
    },
    data: qs.stringify(datas)
  })
}



