<template>
  <div id="app" v-cloak v-loading.fullscreen.lock="fullscreenLoading">
    <div class="header-title">支付管理平台</div>
    <div class="country_select">
      <template>
        <el-select v-model="countrySelect" placeholder="请选择国家" @change="changeCountry()" style="width: 6rem">
          <el-option
            v-for="item in this.options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-select v-model="cpInfoSelect" placeholder="请选择商户" @change="changeCpInfo()" style="width: 12rem">
          <el-option value="">请选择商户</el-option>
          <el-option
            v-for="item in this.cpInfoOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </template>
    </div>
    <el-row :span="24" :gutter="12" style="margin-top: 0.5rem">
      <div>
        <el-col :xl="24" :lg="24" :md="24" :sm="24" :xs="24">
          <div>
            <el-row>
              <el-col :xl="8" :lg="8" :md="12" :sm="12" :xs="24">
                <el-card shadow="hover">
                  <h1 style="margin-bottom: 10px">今日</h1>
                  <div class="card-item"><span>代收</span><span>{{Number(showData.todayPay/100).toFixed(2).toLocaleString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')}}</span>
                  </div>
                  <div class="card-item"><span>代收手续费</span><span>{{Number(showData.todayPayFee/100).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}}</span>
                  </div>
                  <div class="card-item"><span>代收订单数</span><span>{{showData.todayPayNum}}</span></div>
                  <div class="card-item"><span>代付</span><span>{{Number(showData.todayWithdraw/100).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}}</span>
                  </div>
                  <div class="card-item"><span>代付手续费</span><span>{{Number(showData.todayWithdrawFee/100).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}}</span>
                  </div>
                  <div class="card-item"><span>代付订单数</span><span>{{showData.todayWithdrawNum}}</span>
                  </div>
                </el-card>
              </el-col>
              <el-col :xl="8" :lg="8" :md="12" :sm="12" :xs="24">
                <el-card shadow="hover">
                  <h1 style="margin-bottom: 10px">昨日</h1>
                  <div class="card-item"><span>代收</span><span>{{Number(showData.yestodayPay/100).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}}</span>
                  </div>
                  <div class="card-item"><span>代收手续费</span><span>{{Number(showData.yestodayPayFee/100).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}}</span>
                  </div>
                  <div class="card-item"><span>代收订单数</span><span>{{showData.yestodayPayNum}}</span></div>
                  <div class="card-item"><span>代付</span><span>{{Number(showData.yestodayWithdraw/100).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}}</span>
                  </div>
                  <div class="card-item"><span>代付手续费</span><span>{{Number(showData.yestodayWithdrawFee/100).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}}</span>
                  </div>
                  <div class="card-item"><span>代付订单数</span><span>{{showData.yestodayWithdrawNum}}</span>
                  </div>
                </el-card>
              </el-col>
              <el-col :xl="8" :lg="8" :md="12" :sm="12" :xs="24">
                <el-card shadow="hover">
                  <h1 style="margin-bottom: 10px">全部</h1>
                  <div class="card-item"><span>代收</span><span>{{Number(showData.totalPay/100).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}}</span>
                  </div>
                  <div class="card-item"><span>代收手续费</span><span>{{Number(showData.totalPayFee/100).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}}</span>
                  </div>
                  <div class="card-item"><span>代收订单数</span><span>{{showData.totalPayNum}}</span></div>
                  <div class="card-item"><span>代付</span><span>{{Number(showData.totalWithdraw/100).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}}</span>
                  </div>
                  <div class="card-item"><span>代付手续费</span><span>{{Number(showData.totalWithdrawFee/100).toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ',')}}</span>
                  </div>
                  <div class="card-item"><span>代付订单数</span><span>{{showData.totalWithdrawNum}}</span>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </div>
        </el-col>
      </div>
      <el-col :xl="8" :lg="8" :md="8" :sm="8" :xs="8">

      </el-col>
      <el-col :xl="8" :lg="8" :md="8" :sm="8" :xs="8">

      </el-col>
    </el-row>

    <el-row :span="24" :gutter="12" style="padding-top: 30px;">
      <div id="cpinfoDailyStatistics" style="width: auto;height:400px;"></div>
    </el-row>
    <el-row :span="24" :gutter="12" style="padding-top: 30px;">
      <div id="cpinfoHour24Statictics" style="width: auto;height:400px;"></div>
    </el-row>
    <el-row :gutter="12" style="padding-top: 20px;">
      <el-col :span="16">
        <div id="main" style="height:400px;width: 100%"></div>
      </el-col>
      <el-col :span="8">
        <el-table :data="warnData" stripe style="width: 100%" max-height="390px">
          <el-table-column type="index" label="#"></el-table-column>
          <el-table-column label="类别" width="80">
            <template slot-scope="scope">
              <el-tag type="danger">{{scope.row.type}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="info" label="详情"></el-table-column>
        </el-table>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import {
    queryCpInfoSelectList,
    getHomeStatistics,
    homeWarningInfo,
    cpinfoDailyStatistics,
    cpinfoHour24Statistics,
    queryCpInfoByCountrySelectList
  } from "../api/cpinfo"
  import {queryCountryPageList} from "../api/dictionary";
  import {toDecimalString} from "../statics/custom"

  const echarts = require('echarts');


  export default {
    name: "Home",
    async created() {
      this.countrySelect = 'IND'
      await this.homeStatistics()
      this.fullscreenLoading = false
      // await this.homeEcharts()
      this.homeWarningInfo()
      this.myChart = echarts.init(document.getElementById('cpinfoDailyStatistics'))
      this.myCpinfoHour24Chart = echarts.init(document.getElementById('cpinfoHour24Statictics'))
      this.queryCountryPageList();
      this.queryCpInfoByCountrySelectList();
      this.cpinfoDailyStatistics();
      this.cpinfoHour24Statistics();
    },
    data() {
      return {
        timer: '',
        value: 20,

        fullscreenLoading: true,
        showData: {
          todayPay: 0,
          todayPayFee: 0,
          todayPayNum: 0,
          todayWithdraw: 0,
          todayWithdrawFee: 0,
          todayWithdrawNum: 0,
          totalPay: 0,
          totalPayFee: 0,
          totalPayNum: 0,
          totalWithdraw: 0,
          totalWithdrawFee: 0,
          totalWithdrawNum: 0,
          yestodayPay: 0,
          yestodayPayFee: 0,
          yestodayPayNum: 0,
          yestodayWithdraw: 0,
          yestodayWithdrawFee: 0,
          yestodayWithdrawNum: 0,

        },

        myChart: {},
        polar: {
          title: {
            text: '今日商户数据展示'
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {            // 坐标轴指示器，坐标轴触发有效
              type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          legend: {
            show: true, //是否显示
            type: "plain", // 图例的类型 'plain':普通图例  'scroll':可滚动翻页的图例
            zlevel: 1, // 所有图形的 zlevel 值。
            icon: "circle",
            top: "5%", // bottom:"20%" // 组件离容器的距离
            right: "5%", //left:"10%"  // // 组件离容器的距离
            width: "auto", // 图例组件的宽度
            height: "auto", // 图例组件的高度
            orient: "horizontal", // 图例列表的布局朝向。 'horizontal'  'vertical'
            align: "auto", // 图例标记和文本的对齐
            padding: 5, // 图例内边距
            itemWidth: 12, // 图例标记的图形宽度。
            itemGap: 20, // 图例每项之间的间隔。
            itemHeight: 14, //  图例标记的图形高度。
            symbolKeepAspect: true, // 如果图标（可能来自系列的 symbol 或用户自定义的 legend.data.icon）是 path:// 的形式，是否在缩放时保持该图形的长宽比。
            formatter: function (name) {
              return name
            },
            selectedMode: true, // 图例选择的模式，
            inactiveColor: "#ccc", // 图例关闭时的颜色。
            textStyle: {
              color: "#556677", // 文字的颜色。
              fontStyle: "normal", // 文字字体的风格。
              fontWeight: "normal", // 文字字体的粗细。 'normal' 'bold'  'bolder' 'lighter'  100 | 200 | 300 | 400...
              fontFamily: "sans-serif", // 文字的字体系列。
              fontSize: 12, // 文字的字体大小。
              lineHeight: 20, // 行高。
              backgroundColor: "transparent", // 文字块背景色。
              borderColor: "transparent", // 文字块边框颜色。
              borderWidth: 0, // 文字块边框宽度。
              borderRadius: 0, // 文字块的圆角。
              padding: 0, // 文字块的内边距
              shadowColor: "transparent", // 文字块的背景阴影颜色
              shadowBlur: 0, // 文字块的背景阴影长度。
              shadowOffsetX: 0, // 文字块的背景阴影 X 偏移。
              shadowOffsetY: 0, // 文字块的背景阴影 Y 偏移。
              // width: 50, // 文字块的宽度。 默认
              // height: 40, // 文字块的高度 默认
              textBorderColor: "transparent", // 文字本身的描边颜色。
              textBorderWidth: 0, // 文字本身的描边宽度。
              textShadowColor: "transparent", // 文字本身的阴影颜色。
              textShadowBlur: 0, // 文字本身的阴影长度。
              textShadowOffsetX: 0, // 文字本身的阴影 X 偏移。
              textShadowOffsetY: 0, // 文字本身的阴影 Y 偏移。
              rich: {
                a: {
                  color: "red",
                  lineHeight: 10
                },
                b: {
                  color: "#fff",
                  lineHeight: 10,
                },
              }, // 自定富文本样式
            },
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: [
            {
              type: 'category',
              data: []
            }
          ],
          yAxis: [
            {
              type: 'value'
            }
          ],
          series: [{
            name: '代收',
            type: 'bar',
            data: [],
            itemStyle: {
              normal: {
                color: 'rgb(151,207,124)',
                label: {
                  show: true, //开启显示
                  position: 'top', //在上方显示
                  textStyle: { //数值样式
                    color: 'rgb(151,207,124)',
                    fontSize: 14
                  },
                  formatter: function (data) {
                    return toDecimalString(data.data * 100)
                  }
                }
              }
            },
          },
            {
              name: '代付',
              type: 'bar',
              data: [],
              itemStyle: {
                normal: {
                  color: '#f03e41',
                  label: {
                    show: true, //开启显示
                    position: 'top', //在上方显示
                    textStyle: { //数值样式
                      color: '#f03e41',
                      fontSize: 14
                    },
                    formatter: function (data) {
                      return toDecimalString(data.data * 100)
                    }
                  }
                }
              },
            }
          ],
          dataZoom: [
            {
              type: 'inside',  //slider有滑块，inside内置
              disabled: false,  //是否停止组件功能
              xAxisIndex: 0,  //x轴,可以用数组表示多个轴
              zoomLock: true,  //是否锁定区域大小（true,只能平移不能缩放）
              preventDefaultMouseMove: false,
              filterMode: "empty",
              startValue: 0,  //一行有几个（起始数组下标）
              endValue: 4,    //一行有几个（结束数组下标）
              start: null,
              end: null,
            },
          ]
        },
        myCpinfoHour24Chart: {},
        cpinfoHour24: {
          title: {
            text: '商户24小时数据变化图'
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {            // 坐标轴指示器，坐标轴触发有效
              type: 'line'        // 默认为直线，可选为：'line' | 'shadow'
            }
          },
          legend: {
            show: true, //是否显示
            type: "plain", // 图例的类型 'plain':普通图例  'scroll':可滚动翻页的图例
            zlevel: 1, // 所有图形的 zlevel 值。
            icon: "circle",
            top: "5%", // bottom:"20%" // 组件离容器的距离
            right: "5%", //left:"10%"  // // 组件离容器的距离
            width: "auto", // 图例组件的宽度
            height: "auto", // 图例组件的高度
            orient: "horizontal", // 图例列表的布局朝向。 'horizontal'  'vertical'
            align: "auto", // 图例标记和文本的对齐
            padding: 5, // 图例内边距
            itemWidth: 12, // 图例标记的图形宽度。
            itemGap: 20, // 图例每项之间的间隔。
            itemHeight: 14, //  图例标记的图形高度。
            symbolKeepAspect: true, // 如果图标（可能来自系列的 symbol 或用户自定义的 legend.data.icon）是 path:// 的形式，是否在缩放时保持该图形的长宽比。
            formatter: function (name) {
              return name
            },
            selectedMode: true, // 图例选择的模式，
            inactiveColor: "#ccc", // 图例关闭时的颜色。
            textStyle: {
              color: "#556677", // 文字的颜色。
              fontStyle: "normal", // 文字字体的风格。
              fontWeight: "normal", // 文字字体的粗细。 'normal' 'bold'  'bolder' 'lighter'  100 | 200 | 300 | 400...
              fontFamily: "sans-serif", // 文字的字体系列。
              fontSize: 12, // 文字的字体大小。
              lineHeight: 20, // 行高。
              backgroundColor: "transparent", // 文字块背景色。
              borderColor: "transparent", // 文字块边框颜色。
              borderWidth: 0, // 文字块边框宽度。
              borderRadius: 0, // 文字块的圆角。
              padding: 0, // 文字块的内边距
              shadowColor: "transparent", // 文字块的背景阴影颜色
              shadowBlur: 0, // 文字块的背景阴影长度。
              shadowOffsetX: 0, // 文字块的背景阴影 X 偏移。
              shadowOffsetY: 0, // 文字块的背景阴影 Y 偏移。
              // width: 50, // 文字块的宽度。 默认
              // height: 40, // 文字块的高度 默认
              textBorderColor: "transparent", // 文字本身的描边颜色。
              textBorderWidth: 0, // 文字本身的描边宽度。
              textShadowColor: "transparent", // 文字本身的阴影颜色。
              textShadowBlur: 0, // 文字本身的阴影长度。
              textShadowOffsetX: 0, // 文字本身的阴影 X 偏移。
              textShadowOffsetY: 0, // 文字本身的阴影 Y 偏移。
              rich: {
                a: {
                  color: "red",
                  lineHeight: 10
                },
                b: {
                  color: "#fff",
                  lineHeight: 10,
                },
              }, // 自定富文本样式
            },
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: [
            {
              type: 'category',
              data: []
            }
          ],
          yAxis: [
            {
              type: 'value'
            }
          ],
          series: [{
            name: '代收',
            type: 'line',
            smooth: true,
            data: [],
            label: {//线条上的数字提示信息
              normal: {
                show: false,
                position: 'top'
              }
            },
            itemStyle: {
              normal: {
                color: 'rgb(151,207,124)'
              }
            },
          },
            {
              name: '代付',
              type: 'line',
              smooth: true,
              data: [],
              itemStyle: {
                normal: {
                  color: '#f03e41'
                }
              }
            }
          ],
          cpinfo: {
            country: '',
            appId: ''
          },
          dataZoom: [
            {
              type: 'inside',  //slider有滑块，inside内置
              disabled: false,  //是否停止组件功能
              xAxisIndex: 0,  //x轴,可以用数组表示多个轴
              zoomLock: true,  //是否锁定区域大小（true,只能平移不能缩放）
              preventDefaultMouseMove: false,
              filterMode: "empty",
              startValue: 20,  //一行有几个（起始数组下标）
              endValue: 28,    //一行有几个（结束数组下标）
              start: null,
              end: null,
            },
          ]
        },
        warnData: [],
        options: [],
        cpInfoOptions: [],
        countrySelect: '',
        cpInfoSelect: '',
      }
    },
    methods: {
      homeStatistics() {
        var that = this
        getHomeStatistics({country: that.countrySelect, appId: that.cpInfoSelect}).then(function (response) {
          if (response.data.code === 0) {
            that.showData = response.data.data
          }

        })
      },
      homeWarningInfo() {
        let that = this
        return new Promise(resolve => {
          homeWarningInfo().then(function (response) {
            if (response.data.code === 0) {
              that.warnData = response.data.data
            }
          })
        });
      },
      cpinfoDailyStatistics() {
        let that = this
        return new Promise(resolve => {
          cpinfoDailyStatistics({country: that.countrySelect, appId: that.cpInfoSelect}).then(function (response) {
            if (response.data.code === 0) {

              var dailyStatistics = response.data.data

              const cpInfoAppIds = []
              const cpInfoPays = []
              const cpInfoWithdraws = []

              for (const cpInfoDailyStatistics of dailyStatistics) {
                cpInfoAppIds.push(cpInfoDailyStatistics.appId)
                cpInfoPays.push(cpInfoDailyStatistics.payAmount)
                cpInfoWithdraws.push(cpInfoDailyStatistics.withdrawAmount)
              }

              that.polar.legend.data = response.data.data.legend
              that.polar.xAxis[0].data = []
              that.polar.series[0].data = []
              that.polar.series[1].data = []
              that.polar.xAxis[0].data = cpInfoAppIds
              that.polar.series[0].data = cpInfoPays
              that.polar.series[1].data = cpInfoWithdraws
              that.myCpinfoHour24Chart.setOption(that.polar);
            }

          })
        });
      },
      cpinfoHour24Statistics() {
        let that = this
        return new Promise(resolve => {
          cpinfoHour24Statistics({country: that.countrySelect, appId: that.cpInfoSelect}).then(function (response) {
            if (response.data.code === 0) {

              var cpInfoHour24Statistics = response.data.data
              var times = []
              var payAmounts = []
              var withdrawAmounts = []

              for (const cpInfoHour24Statistic of cpInfoHour24Statistics) {
                times.push(cpInfoHour24Statistic.time)
                payAmounts.push(cpInfoHour24Statistic.payAmount)
                withdrawAmounts.push(cpInfoHour24Statistic.withdrawAmount)
              }

              // that.polar.legend.data = response.data.data.legend
              that.cpinfoHour24.xAxis[0].data = []
              that.cpinfoHour24.series[0].data = []
              that.cpinfoHour24.series[1].data = []
              that.cpinfoHour24.xAxis[0].data = times
              that.cpinfoHour24.series[0].data = payAmounts
              that.cpinfoHour24.series[1].data = withdrawAmounts
              that.myChart.setOption(that.cpinfoHour24);
            }
          })
        });
      },
      queryCountryPageList() {
        let that = this
        that.options = []
        return new Promise(resolve => {
          queryCountryPageList({type: 'country'}).then(function (res) {
            if (res.data.code === 0) {
              that.options = []
              res.data.data.forEach((item, index) => {

                that.options.push(
                  {
                    "label": item.name,
                    "value": item.value
                  }
                )
              })
            }
          })
        })
      },
      queryCpInfoByCountrySelectList() {
        let that = this
        return new Promise(resolve => {
          queryCpInfoByCountrySelectList({country: that.countrySelect}).then(function (res) {
            that.cpInfoOptions = []
            if (res.data.code === 0) {
              res.data.data.forEach((item, index) => {
                that.cpInfoOptions.push({
                  "label": item.appId + ' ' + item.userName,
                  "value": item.appId
                })
              })
            }
          })
        })
      },
      changeCountry() {
        this.homeStatistics()
        this.cpinfoDailyStatistics()
        this.cpinfoHour24Statistics()
        this.queryCpInfoByCountrySelectList()
      },
      changeCpInfo() {
        this.homeStatistics()
        this.cpinfoDailyStatistics()
        this.cpinfoHour24Statistics()
      }
    },
    mounted() {
      this.timer = setInterval(() => {
        this.homeStatistics()
        this.cpinfoDailyStatistics()
        this.cpinfoHour24Statistics()
      }, 20 * 1000)
      // this.timer = setInterval(this.cpinfoDailyStatistics(), 20*1000);
      // this.timer = setInterval(this.cpinfoHour24Statistics(), 20*1000);

    },

    beforeDestroy() {
      clearInterval(this.timer);
    }
  }
</script>

<style scoped>

</style>
<style>
  .header-title {
    text-align: center;
    font-size: 40px;
    margin: 30px 0;
  }

  .card-item {
    font-size: 22px;
  }

  .card-item > span:first-child {
    margin: 5px;
    display: inline-block;
  }

  .card-item > span:last-child {
    float: right;
    right: 10%;
    position: relative;
    font-weight: bold;
  }

  .country_select {
    text-align: right;
    margin-right: 1rem;
  }
</style>



