<template>
  <div id="app" v-cloak v-loading.fullscreen.lock="fullscreenLoading">
    <el-form :inline="true" :model="searchForm" ref="searchForm">
      <el-form-item label="用户" prop="userName">
        <el-input v-model="searchForm.userName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search">查询</el-button>
        <el-button @click="resetForm('searchForm')">重置</el-button>
      </el-form-item>
    </el-form>
    <el-row>
      <el-button plain @click="addUser()">添加用户</el-button>
    </el-row>
    <el-table
      :data="tableData" size="small"
      style="width: 100%">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="密码">
              <span>{{ props.row.password }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        prop="username"
        label="用户名">
      </el-table-column>
      <el-table-column
        prop="roleName"
        label="角色">
      </el-table-column>
      <el-table-column
        prop="logintime"
        label="上次登陆时间">
      </el-table-column>
      <el-table-column
        prop="createtime"
        label="创建时间">
      </el-table-column>

      <el-table-column label="启用/禁用账号">
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.status==1?'禁用':'启用'" placement="top">
            <el-switch
              v-model="scope.row.status"
              active-color="#13ce66"
              inactive-color="#ff4949"
              :active-value="1"
              :inactive-value="-1"
              @change="changeStatus(scope.row)">
            </el-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <el-table-column label="操作" width="300">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div style="text-align: center;margin-top:5px;">
      <el-pagination
        background
        layout="total, prev, pager, next"
        :total="total" :current-page="searchForm.page" @current-change="pageChange">
      </el-pagination>
    </div>
    <!-- Form -->
    <el-dialog title="添加用户" :modal="false" :visible.sync="dialog.visible" width="24rem" @closed="closeDialog">
      <el-form ref="ruleForm" :model="dialog.form" status-icon :rules="dialog.rules" size="mini">
        <el-form-item label="用户账号" :label-width="dialog.formLabelWidth" prop="userName">
          <el-input v-model="dialog.form.userName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" :label-width="dialog.formLabelWidth" prop="password">
          <el-input type="password" v-model="dialog.form.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" :label-width="dialog.formLabelWidth" prop="passwordNew">
          <el-input type="password" v-model="dialog.form.passwordNew" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="所属角色" :label-width="dialog.formLabelWidth"
                      prop="role">
          <el-select v-model="dialog.form.role" placeholder="请选择" filterable>
            <el-option style="color: #8492a6; font-size: 13px" label="请选择" value=""></el-option>
            <el-option
              v-for="(item) in this.roleOptions"
              :key="item.label"
              :label="item.label"
              :value="item.value">
              <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialog.visible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import {addUser, changeStatus, deleteUser, queryUserPageList, updateUser} from "../../api/user";
  import {queryRoleSelectList} from "../../api/role";

  export default {
    name: "userList",
    data() {
      let validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.dialog.form.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      let validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.dialog.form.UserPass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      let isNumber = (rule, value, callback) => {
        if (typeof value == 'number') {
          callback();
          return;
        }
        if (value == null || value.indexOf(' ') != -1) {
          callback(new Error('必须是数字!'));
          return;
        }
        if (!isNaN(value)) {
          callback();
        } else {
          callback(new Error('必须是数字!'));
        }
      }
      //代理商费用判断，不能高于商户
      let agentPayFeeFix = (rule, value, callback) => {
        if (value <= this.dialog.form.payFeeFix) {
          callback()
        } else {
          callback(new Error('代理商费用应小于商户!'));
        }
      }
      let agentPayFeeRate = (rule, value, callback) => {
        if (value <= this.dialog.form.payFeeRate) {
          callback()
        } else {
          callback(new Error('代理商费用应小于商户!'));
        }
      }
      let agentWithdrawFeeFix = (rule, value, callback) => {
        if (value <= this.dialog.form.withdrawFeeFix) {
          callback()
        } else {
          callback(new Error('代理商费用应小于商户!'));
        }
      }
      let agentWithdrawFeeRate = (rule, value, callback) => {
        if (value <= this.dialog.form.withdrawFeeRate) {
          callback()
        } else {
          callback(new Error('代理商费用应小于商户!'));
        }
      }
      let country = (rule, value, callback) => {
        if (this.dialog.form.country !== '') {
          callback()
        } else {
          callback(new Error('请选择一个国家!'));
        }
      }
      return {
        fullscreenLoading: true,
        searchForm: {
          userName: '',
          page: 1,
          limit: 10
        },
        total: 0,
        dialog: {
          isInsert: true,
          visible: false,
          formLabelWidth: '150px',
          form: {
            id: '',
            userName: '',
            password: '',
            passwordNew: '',
            role: ''
          },
          rules: {
            UserName: [
              {required: true, message: '请输入账号名称', trigger: 'blur'},
              {min: 3, max: 50, message: '长度在 3 到 50 个字符', trigger: 'blur'}
            ],
            UserPass: [
              {required: true, message: '请输入账号密码', trigger: 'blur'},
              {validator: validatePass, trigger: 'blur'}
            ],
            checkPass: [
              {validator: validatePass2, trigger: 'blur'}
            ],
            SubRatio: [
              {required: true, message: '请输入', trigger: 'blur'},
            ],
            RemainDelayDay: [
              {required: true, message: '请输入', trigger: 'blur'},
            ],
            AgentpayFeeFix: [
              {validator: isNumber, trigger: 'blur'},
              {validator: agentPayFeeFix, trigger: 'blur'}
            ],
            AgentpayFeeRate: [
              {validator: isNumber, trigger: 'blur'},
              {validator: agentPayFeeRate, trigger: 'blur'}
            ],
            AgentwithdrawFeeFix: [
              {validator: isNumber, trigger: 'blur'},
              {validator: agentWithdrawFeeFix, trigger: 'blur'}
            ],
            AgentwithdrawFeeRate: [
              {validator: isNumber, trigger: 'blur'},
              {validator: agentWithdrawFeeRate, trigger: 'blur'}
            ],
          }
        },
        settings: {
          visible: false,
          activeName: 'first',
          userName: '',
          appId: 0,
          payFix: 0,
          payRate: 0,
          withdrawFix: 0,
          withdrawRate: 0,
          balanceForm: {
            password: '',
            Verifycode: '',
            remain: 0,
            Notes: ''
          },
          creditForm: {
            password: '',
            Verifycode: '',
            remain: '0',
          },
          agentForm: {
            UserName: ''
          },
          typeForm: {
            payType: 0,
            withdrawType: 0,
          },

          rules: {
            UserName: [
              {required: true, message: '请输入代理商名称', trigger: 'blur'},
            ],
            password: [
              {required: true, message: '请输入密码', trigger: 'blur'},
            ],
            Verifycode: [
              {required: true, message: '请输入验证码', trigger: 'blur'},
            ],
            remain: [
              {required: true, message: '请输入余额变动值', trigger: 'blur'},
              {validator: isNumber, trigger: 'blur'}
            ],
            country: [
              {required: true, message: '请选择一个国家', trigger: 'blur'},
              {validator: isNumber, trigger: 'blur'}
            ]
          },
          payTypeOptions: [],
          withdrawTypeOptions: [],
        },
        tableData: []
      }
    },
    created() {
      this.queryUserPageList()
      this.queryRoleSelectList()
    },
    methods: {
      queryUserPageList() {
        var that = this
        queryUserPageList(that.searchForm).then(function (response) {
          that.fullscreenLoading = false
          if (response.data.code === 0) {

            that.tableData = response.data.data
            that.total = response.data.count
          }
        })
      },
      refresh() {
      },
      pageChange(cur) {
        this.searchForm.page = cur
        this.queryUserPageList()
      },
      search() {
        this.searchForm.page = 1
        this.queryUserPageList()
      },
      submitForm(formName) {
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (formName === 'ruleForm') {
              that.saveUser()
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      saveUser() {
        let str = this.dialog.isInsert ? '添加' : '编辑'
        let that = this
        this.$confirm('确定' + str + '账户?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          (this.dialog.isInsert ? addUser(that.dialog.form) : updateUser(that.dialog.form)).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: str + '成功',
                type: 'success'
              })
              that.queryUserPageList()
            } else {
              str = str + '失败，' + response.data.msg
              that.$message.error(str);
            }
            that.closeDialog()
          })
        })
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      closeDialog() {
        this.dialog.visible = false
        this.dialog.isInsert = true
        this.commonCloseDialog(this.dialog.form, 'ruleForm')
      },
      commonCloseDialog(obj, formName) {
        Object.keys(obj).forEach(key => {
          let type = typeof obj[key]
          if (type === 'number') {
            obj[key] = 0
          }
          if (type === 'string') {
            obj[key] = ''
          }
        })
        this.dialog.form.platformId = ''
        // this.resetForm(formName)
      },
      handleEdit(index, row) {
        let that = this
        Object.keys(row).forEach(key => {
          if (that.dialog.form.hasOwnProperty(key)) {
            that.dialog.form[key] = row[key]
          }
        })
        this.dialog.form.id = row.userId
        this.dialog.form.userName = row.username
        this.dialog.form.password = row.password
        this.dialog.form.passwordNew = row.password
        this.dialog.form.role = row.role
        this.dialog.visible = true
        this.dialog.isInsert = false
      },
      handleDelete(index, row) {
        let that = this
        this.$confirm('此操作将永久删除账户, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          deleteUser({
            id: row.userId
          }).then(function (response) {
            if (response.data.code === 0) {
              that.$message({
                message: '删除成功',
                type: 'success'
              })
            } else {
              that.$message.error('删除失败');
            }
            that.queryUserPageList()
          })
        })

      },
      changeStatus(row) {
        let that = this
        changeStatus({
          id: row.userId,
          status: row.status
        }).then(function (response) {
          if (response.data.code === 0) {
            that.$message({
              message: '修改成功',
              type: 'success'
            })
          } else {
            that.$message.error('修改失败');
          }
          that.refresh()
        })
      },
      changeSettings(row) {
        this.settings.visible = true
        if (row.DefaultPayTypeId.length > 0 && row.DefaultPayTypeId !== 0) {
          let DefaultPayTypeId = row.DefaultPayTypeId.split("、")
          let payTypes = [];
          for (let i = 0; i < DefaultPayTypeId.length; i++) {
            payTypes.push(DefaultPayTypeId[i] + "")
          }
          this.settings.typeForm.payType = payTypes
        } else {
          this.settings.typeForm.payType = []
        }

        if (row.DefaultPayTypeId.length > 0 && row.DefaultPayTypeId !== 0) {
          let defaultWithdrawTypeId = row.DefaultWithdrawTypeId.split("、")
          let withdrawTypes = [];
          for (let i = 0; i < defaultWithdrawTypeId.length; i++) {
            withdrawTypes.push(defaultWithdrawTypeId[i] + "")
          }
          this.settings.typeForm.withdrawType = withdrawTypes
        } else {
          this.settings.typeForm.withdrawType = []
        }
        this.settings.userName = row.UserName
        this.settings.appId = row.appId
        this.settings.payFeeFix = row.payFeeFix
        this.settings.payFeeRate = row.payFeeRate
        this.settings.withdrawFeeFix = row.withdrawFeeFix
        this.settings.withdrawFeeRate = row.withdrawFeeRate
      },
      closeSettingsDialog() {
        this.settings.activeName = 'first'
        this.settings.visible = false
        this.commonCloseDialog(this.settings.balanceForm, 'balanceForm')
        this.commonCloseDialog(this.settings.creditForm, 'creditForm')
        this.commonCloseDialog(this.settings.agentForm, 'agentForm')
        this.commonCloseDialog(this.settings.typeForm, 'typeForm')
      },
      queryRoleSelectList() {
        let that = this
        return new Promise(resolve => {
          queryRoleSelectList().then(function (res) {
            if (res.data.code === 0) {
              that.roleOptions = []
              res.data.data.forEach((item, index) => {
                that.roleOptions.push(
                  {
                    "label": item.rolename,
                    "value": item.id
                  }
                )

              })
            }
          })
        })
      },
      addUser() {
        this.dialog.visible = true
      }
    }
  }
</script>

<style scoped>

</style>



