import Axios from "./axios"  // 导入配置好的axios文件
import qs from  "qs"

export function queryPermissionTreeList(datas) {
  return Axios({
    url: "/permission/queryPermissionTreeList",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //设置请求头请求格式form
    },
    data: qs.stringify(datas)
  })
}

export function getMenuListSelectByRoleId(datas) {
  return Axios({
    url: "/permission/getMenuListSelectByRoleId",
    method: "post",
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded' //设置请求头请求格式form
    },
    data: qs.stringify(datas)
  })
}



