<template>
  <div id="app" style="">
    <!--            查询条件form-->
    <el-form :model="searchForm" ref="searchForm" label-width="90px">
      <el-row>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="系统订单号" prop="id">
            <el-input v-model="searchForm.id" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="商户订单号" prop="orderId">
            <el-input v-model="searchForm.orderId" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="三方订单号" prop="transactionNum">
            <el-input v-model="searchForm.transactionNum" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="订单状态" prop="status">
            <el-select v-model="searchForm.status" placeholder="请选择">
              <el-option label="请选择" value=""></el-option>
              <el-option label="成功" value="1"></el-option>
              <el-option label="待处理" value="0"></el-option>
              <el-option label="失败" value="-1"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="实际状态" prop="status1">
            <el-select v-model="searchForm.status1" placeholder="请选择">
              <el-option label="请选择" value=""></el-option>
              <el-option label="成功" value="1"></el-option>
              <el-option label="待处理" value="0"></el-option>
              <el-option label="失败" value="-1"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="商户" prop="appId">
            <el-input v-model="searchForm.appId" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="通道" prop="payTypeId">
            <el-select v-model="searchForm.payTypeId" placeholder="请选择" filterable>
              <el-option label="请选择" value=""></el-option>
              <el-option
                v-for="(item) in this.payTypeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
                <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="国家" prop="country">
            <el-select v-model="searchForm.country" placeholder="请选择">
              <el-option label="请选择" value=""></el-option>
              <el-option
                v-for="(item) in this.countryOptions"
                :key="item.label"
                :label="item.label"
                :value="item.value">
                <span style="color: #8492a6; font-size: 13px">{{ item.label }}</span>
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="同步状态" prop="syncCnt">
            <el-select v-model="searchForm.syncCnt" placeholder="请选择">
              <el-option label="请选择" value=""></el-option>
              <el-option label="同步成功" value="1"></el-option>
              <el-option label="未回调" value="0"></el-option>
              <el-option label="失败重试" value="-1"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="clabe" prop="clabe">
            <el-input v-model="searchForm.clabe" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="Rastreo" prop="rastreo">
            <el-input v-model="searchForm.rastreo" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="3" :lg="6" :md="6" :sm="8" :xs="24" class="form-search">
          <el-form-item label="金额" prop="amount">
            <el-input v-model="searchForm.amount" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="12" :lg="12" :md="12" :sm="24" :xs="24" class="form-search">
          <el-form-item label="创建时间">
            <el-col :span="11">
              <el-date-picker
                v-model="createTime"
                type="datetimerange"
                :clearable="true"
                :default-time="['00:00:00', '23:59:59']"
                value-format="yyyy-MM-dd HH:mm:ss"
                placeholder="选择起始日期"
                :picker-options="pickerOptionsStartCreate">
              </el-date-picker>
            </el-col>
          </el-form-item>
        </el-col>
        <el-col :xl="12" :lg="12" :md="12" :sm="24" :xs="24" class="form-search">
          <el-form-item label="支付时间">
            <el-col :span="11">
              <el-date-picker
                v-model="payTime"
                type="datetimerange"
                :clearable="true"
                :default-time="['00:00:00', '23:59:59']"
                value-format="yyyy-MM-dd HH:mm:ss"
                placeholder="选择起始日期"
                :picker-options="pickerOptionsStartPay">
              </el-date-picker>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
      <el-form-item class="search-button">
        <el-button size="medium" icon="el-icon-search" type="primary" @click="search">查询</el-button>
        <el-button size="medium" icon="el-icon-refresh-left" @click="resetForm('searchForm')">重置</el-button>
      </el-form-item>
    </el-form>
    <!--            查询统计-->
    <div style="margin-left: 30px">
      <el-row :gutter="20">
        <el-col :xl="6" :lg="6" :md="6" :sm="8" :xs="24">
          <div class="grid-content bg-purple">
                        <span class="show-statictics-span">
                            订单总额：{{searchStatictics.needPayAmount}}
                        </span>
          </div>
        </el-col>
        <el-col :xl="6" :lg="6" :md="6" :sm="8" :xs="24">
          <div class="grid-content bg-purple">
                        <span class="show-statictics-span">
                            实收订单总额：{{searchStatictics.payAmount}}
                        </span>
          </div>
        </el-col>
        <el-col :xl="6" :lg="6" :md="6" :sm="8" :xs="24">
          <div class="grid-content bg-purple">
                        <span class="show-statictics-span">
                            实收手续费：{{searchStatictics.payFee}}
                        </span>
          </div>
        </el-col>
        <el-col :xl="6" :lg="6" :md="6" :sm="8" :xs="24">
          <div class="grid-content bg-purple">
                        <span class="show-statictics-span">
                            实收订单数：{{searchStatictics.payNum}}
                        </span>
          </div>
        </el-col>
      </el-row>
    </div>
    <!--            数据表格-->
    <div id="grid" style="width: 100%">
      <el-table
        :data="tableData"
        style="width: 100%"
        size="small">
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="跳转地址">
                <span>{{ props.row.callbackUrl }}</span>
              </el-form-item>
              <el-form-item label="回调地址">
                <span>{{ props.row.notifyUrl }}</span>
              </el-form-item>
              <el-form-item label="Ip">
                <span>{{ props.row.ip }}</span>
              </el-form-item>
              <el-form-item label="备注">
                <span>{{ props.row.description }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          prop="id"
          label="订单号">
        </el-table-column>
        <el-table-column
          prop="orderId"
          label="下游订单号">
        </el-table-column>
        <el-table-column
          prop="transactionNum"
          label="第三方订单号">
        </el-table-column>
        <el-table-column
          prop="appId"
          label="商户Id">
        </el-table-column>
        <el-table-column
          prop="amount"
          label="金额">
        </el-table-column>
        <el-table-column
          prop="platformFee"
          label="商户手续费">
        </el-table-column>
        <el-table-column
          prop="thirdFee"
          label="第三方手续费">
        </el-table-column>
        <el-table-column
          prop="payTypeName"
          label="充值通道">
        </el-table-column>
        <el-table-column
          prop="type"
          label="充值类型">
        </el-table-column>
        <el-table-column
          label="订单状态">
          <template slot-scope="scope">
                    <span v-if="scope.row.status  === '成功'"
                          style="color:red;font-weight: bolder">{{scope.row.status}}</span>
            <span v-else style="color:#606972">{{scope.row.status}}</span>
          </template>
        </el-table-column>
        <el-table-column
          label="实际状态">
          <template slot-scope="scope">
                    <span v-if="scope.row.status1  === '成功'"
                          style="color:red;font-weight: bolder">{{scope.row.status1}}</span>
            <span v-else style="color:#606972">{{scope.row.status1}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="syncCnt"
          label="回调状态">
        </el-table-column>
        <el-table-column
          prop="validStatus"
          label="结算状态">
        </el-table-column>
        <el-table-column
          prop="createTime"
          label="创建时间">
        </el-table-column>
        <el-table-column
          prop="payTime"
          label="支付时间">
        </el-table-column>
        <el-table-column
          prop="createTime"
          label="结算日期">
        </el-table-column>
        <el-table-column
          prop="clabe"
          label="clabe">
        </el-table-column>
        <el-table-column
          prop="rastreo"
          label="rastreo">
        </el-table-column>
        <el-table-column label="操作" width="300">
          <template slot-scope="scope">
            <el-button :disabled="!(scope.row.status === '待处理' )" type="danger"
                       round
                       @click="changeStatus(scope)"
                       @change="">回调
            </el-button>
            <el-button :disabled="(scope.row.status === '待处理' )"
                       type="success"
                       round
                       @click="openSendNotifyMsgDrawer(scope.row)"
                       @change="">补发通知
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--            分页-->
    <!--            <div style="text-align: center;margin-top:5px;">-->
    <!--                <el-pagination-->
    <!--                        background-->
    <!--                        layout="total, prev, pager, next"-->
    <!--                        :total="total" :current-page="searchForm.page" @current-change="pageChange">-->
    <!--                </el-pagination>-->
    <!--            </div>-->
    <div style="text-align: center;margin-top:5px;height: 100%">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="pageChange"
        :current-page.sync="searchForm.page"
        :page-size="searchForm.limit"
        :page-sizes="[10, 20, 50, 100, 200]"
        layout="total, sizes, prev, pager, next"
        :total="total">
      </el-pagination>
    </div>
    <!--            发送回调-->
    <el-dialog
      title="请输入密码"
      :visible.sync="centerDialogVisible"
      width="30%"
      center>
      <el-dialog
        width="30%"
        title="请输入验证码"
        :visible.sync="innerVisible"
        append-to-body>
        <el-input v-model="verifyCode" placeholder=""></el-input>
        <el-button @click="innerVisible = false">取 消</el-button>
        <el-button type="primary" @click="checkVerifyCode()">确 定</el-button>
      </el-dialog>
      <el-input v-model="password" placeholder=""></el-input>
      <span slot="footer" class="dialog-footer">
    <el-button @click="centerDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="checkPassword()">确 定</el-button>
  </span>
    </el-dialog>
    <el-drawer
      :visible.sync="sendNotifyDrawerVisable"
      :direction="direction"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :show-close="false"
      custom-class="demo-drawer"
      style="overflow-y: auto"
      size="50%">
      <div class="" style="padding: 20px">
        <el-form>
          <el-form-item label="回调单号" :label-width="formLabelWidth">
            <el-input readonly v-model="sendNotifyMsgForm.id" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="回调地址" :label-width="formLabelWidth">
            <el-input readonly v-model="sendNotifyMsgForm.url" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="回调数据" :label-width="formLabelWidth">
            <el-input readonly v-model="sendNotifyMsgForm.notifyData" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="回调响应" :label-width="formLabelWidth">
            <el-input type="textarea" style="resize: none" readonly :rows="12"
                      v-model="sendNotifyMsgForm.notifyResp"
                      autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="回调分析" :label-width="formLabelWidth">
            <el-input type="textarea" style="resize: none" readonly :rows="3"
                      v-model="sendNotifyMsgForm.notifyAnalyze"
                      autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button :disable="sending" type="success" @click="sendNotifyMsg(sendNotifyMsgForm.id)">{{sending
            ?
            '发送中...' : '发送'}}
          </el-button>
          <el-button @click="cancelNotifyMsgDrawer()">取 消</el-button>
        </div>
      </div>
    </el-drawer>

  </div>
</template>

<script>
  import {changePayLogStatus, queryPayLogPageList, sendNotifyMsg} from "../../api/paylog";
  import {queryCountryPageList} from "../../api/dictionary";
  import {queryPayTypeSelectList} from "../../api/paytype";
  import {checkPassword, checkVerifyCode} from "../../api/user";
  import {formatPayType, formatAmount, dateFormate} from "../../statics/custom"


  export default {
    name: "payLogList",
    data() {
      return {
        fullscreenLoading: false,
        centerDialogVisible: false,
        innerVisible: false,
        sendNotifyDrawerVisable: false,
        direction: 'rtl',
        password: '',
        verifyCode: '',
        notifyId: '',
        createTime: [],
        payTime: [],
        countryOptions: [],
        payTypeOptions: [],
        searchFormOri: {},
        searchForm: {
          id: '',
          appId: '',
          orderId: '',
          transactionNum: '',
          payTypeId: '',
          type: '',
          status: '',
          status1: '',
          syncCnt: '',
          startCreateTime: '',
          endCreateTime: '',
          startPayTime: '',
          endPayTime: '',
          country: '',
          clabe: '',
          rastreo: '',
          amount: '',
          page: 1,
          limit: 10,
          rowindex: 0
        },
        dialog: {
          isInsert: true,
          visible: false,
          formLabelWidth: '150px',
          form: {},
          settings: {
            visible: false,
            activeName: 'first',
            userName: '',
            appId: 0,
            payFix: 0,
            payRate: 0,
            withdrawFix: 0,
            withdrawRate: 0,
            balanceForm: {
              password: '',
              Verifycode: '',
              remain: 0,
              Notes: ''
            }
          },
          rules: {
            UserName: [
              {required: true, message: '请输入账号名称', trigger: 'blur'},
              {min: 3, max: 50, message: '长度在 3 到 50 个字符', trigger: 'blur'}
            ]
          },
        },
        tableData: [],
        total: 0,
        pickerOptionsStartCreate: {},
        pickerOptionsEndCreate: {
          disabledDate(time) {
            if (this.searchForm.startCreateTime) {
              // 让用户可以选择开始结束同一天
              var startCreateTime = new Date(this.searchForm.startCreateTime).valueOf() - 8.64e7 + 1;
            } else {
              var startCreateTime = Date.now() - 8.64e7;
            }
            return time && time.valueOf() < startCreateTime
          }
        },
        pickerOptionsStartPay: {},
        pickerOptionsEndPay: {
          disabledDate(time) {
            if (this.searchForm.startPayTime) {
              // 让用户可以选择开始结束同一天
              var startPayTime = new Date(this.searchForm.startPayTime).valueOf() - 8.64e7 + 1;
            } else {
              var startPayTime = Date.now() - 8.64e7;
            }
            return time && time.valueOf() < startPayTime
          }
        },
        notifyBtnSwitch: 'disabled',
        sendNotifyMsgForm: {
          id: '',
          url: '',
          notifyData: '',
          notifyResp: '',
          notifyAnalyze: ''
        },
        formLabelWidth: 120,
        sending: false,
        searchStatictics: {
          needPayAmount: 0,
          payAmount: 0,
          payFee: 0,
          payNum: 0
        }
      }
    },
    created() {
      this.searchFormOri = this.searchForm
      this.queryAllPayLog()
      this.queryCountryPageList()
      this.queryAllPaytypeList()
    },
    methods: {
      queryAllPayLog() {
        const that = this;
        that.fullscreenLoading = true
        this.searchForm.startCreateTime = this.createTime === "" ? "" : this.createTime[0]
        this.searchForm.endCreateTime = this.createTime === "" ? "" : this.createTime[1]
        this.searchForm.startPayTime = this.payTime === "" ? "" : this.payTime[0]
        this.searchForm.endPayTime = this.payTime === "" ? "" : this.payTime[1]

        return new Promise(resolve => {


          queryPayLogPageList(that.searchForm).then(function (response) {
            if (response.data.code === 0) {
              const tableData = [];

              const statictics = response.data.data.statictics;
              const list = response.data.data.list;

              for (const paylog of list) {

                paylog.type = formatPayType(paylog)

                paylog.amount = formatAmount(paylog.amount)
                paylog.platformFee = formatAmount(paylog.platformFee)
                paylog.thirdFee = formatAmount(paylog.thirdFee)

                var status = paylog.status
                status = status === 1 ? '成功' : status === 0 ? '待处理' : '失败'
                paylog.status = status
                var status1 = paylog.status1
                status1 = status1 === 1 ? '成功' : status1 === 0 ? '待处理' : '失败'
                paylog.status1 = status1

                var syncCnt = paylog.syncCnt
                syncCnt = syncCnt === 1 ? '同步成功' : syncCnt === 0 ? '未回调' : '同步失败' + (syncCnt * -1) + '次'
                paylog.syncCnt = syncCnt

                var validStatus = paylog.validStatus
                validStatus = validStatus === 1 ? '已结算' : '未结算'
                paylog.validStatus = validStatus

                paylog.createTime = dateFormate(paylog.createTime)
                paylog.payTime = dateFormate(paylog.payTime)

                tableData.push(paylog)
              }

              that.searchStatictics.needPayAmount = formatAmount(statictics.needPayAmount)
              that.searchStatictics.payAmount = formatAmount(statictics.payAmount)
              that.searchStatictics.payFee = formatAmount(statictics.payFee)
              that.searchStatictics.payNum = statictics.payNum

              that.tableData = tableData
              that.total = statictics.allPayNum
              that.fullscreenLoading = false
            }
          })

        });
      },
      queryCountryPageList() {
        let that = this
        return new Promise(resolve => {
          queryCountryPageList({type: 'country'}).then(function (res) {
            if (res.data.code === 0) {
              that.countryOptions = []
              res.data.data.forEach((item, index) => {
                that.countryOptions.push(
                  {
                    "label": item.name,
                    "value": item.value
                  }
                )
              })
            }
          })
        })
      },
      queryAllPaytypeList() {
        var that = this
        queryPayTypeSelectList().then(function (res) {
          if (res.data.code === 0) {
            that.payTypeOptions = []
            var data = res.data.data
            for (var paytype of data) {
              that.payTypeOptions.push({
                label: paytype.name,
                value: paytype.id
              })
            }
          }
        })
      },
      search() {
        this.searchForm.page = 1
        this.searchForm.limit = 10
        this.queryAllPayLog()
      },
      resetForm(formName) {
        this.$refs[formName].resetFields()
        this.createTime = []
        this.payTime = []
      },
      pageChange(cur) {
        this.searchForm.page = cur
        this.queryAllPayLog()
      },
      closeDialog() {
        this.dialog.visible = false
        this.dialog.isInsert = true
        this.commonCloseDialog(this.dialog.form, 'ruleForm')
      },
      changeStatus(data) {
        var that = this
        this.centerDialogVisible = true
        that.notifyId = data.row.id

      },
      checkPassword() {
        var that = this
        checkPassword({password: that.password}).then(function (response) {
          if (response.data.code === 0) {
            console.log(response.data)
            that.innerVisible = true
          } else {
            that.$message("CHECK PSSWORD FAIL")
          }
        })
      },
      checkVerifyCode() {
        var that = this
        checkVerifyCode({verifyCode: that.verifyCode}).then(function (response) {
          if (response.data.code === 0) {
            changePayLogStatus({id: that.notifyId}).then(function (response) {
              console.log(response.data)
              if (response.data.code === 0) {
                that.$message(response.data.msg)
                that.notifyId = ''
                that.centerDialogVisible = false
                that.innerVisible = false
                that.queryAllPayLog()
              }
            })
          } else {
            that.$message("VERIFY FAIL")
          }
        })
      },
      handleClose(done) {
        done();
      },

      handleSizeChange(val) {
        this.searchForm.limit = val
        this.queryAllPayLog()
      },
      openSendNotifyMsgDrawer(data) {
        var that = this
        sendNotifyMsg({id: data.id}).then(function (response) {
          if (response.data.code === 0 || response.data.code === -1) {
            that.queryAllPayLog()
          }
          that.sending = false
        })
      },
    }
  }
</script>

<style scoped>

</style>



