<template>
  <div id="app" ref="app" :style="heightClass">
    <el-container>
      <el-aside :width="asideWidth" ref="elaside">
        <el-scrollbar style="width: 100%;height: 100%;overflow-x: hidden">

          <div class="top-info" v-if="!isCollapse">
            <div class="top-info__image">
              <el-image src="./src/assets/topstand.png"
                        fit="cover"></el-image>
            </div>
            <div class="top-info-right">
              <div>TOPSTAND</div>
              <div>PAYMENT</div>
            </div>
          </div>
          <el-menu :collapse-transition="false" default-active="1" :collapse="isCollapse">
            <el-menu-item index="0" @click="getIframeSrc('./homeNew.html')">
              <i class="el-icon-menu"></i>
              <span slot="title" @click="addTabs({ id: 1, permissName: '首页', pValue: 'home' })">首页</span>
            </el-menu-item>

            <el-submenu class="submenu-hover" v-for="item in iframe" :index="item.permissName" :key="item.id">
              <template slot="title">
                <i slot="title" :class="item.pType"></i>
                <span slot="title">{{item.permissName}}</span>
              </template>
              <el-menu-item-group>
                <el-menu-item v-for="itemson in item.children" :index="itemson.permissName"
                              :key="itemson.id" @click="addTabs(itemson)">
                  {{itemson.permissName}}
                </el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-scrollbar>
      </el-aside>

      <el-container>
        <el-header>
          <div class="page-nav-header">
            <div class="header__left" @click="ShowNavMenu">
              <i v-if="isCollapse" class="el-icon-s-unfold" @click="asideWidth = '12rem'"></i>
              <i v-else class="el-icon-s-fold" @click="asideWidth = '4rem'"></i>
            </div>
            <div class="header__right">
              <el-dropdown>
                <div style="cursor: pointer" class="header__right-info">
                  <i class="el-icon-user-solid"></i>
                  <label id="username"></label>
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </div>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item @click.native="checkoutLogin">退出登录</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </div>
          </div>
        </el-header>
        <el-main class="el-main" ref="elmain" style="padding-bottom: 40px">
          <el-row>
            <el-tabs v-model="tabsValues" type="card" editable @edit="handleTabsEdit">
              <el-tab-pane
                :key="item.name"
                v-for="(item) in editableTabs"
                :label="item.title"
                :name="item.name"
              >
                <component :is="item.content"></component>
              </el-tab-pane>
              <!--              <el-tab-pane label="首页" name="1">-->
              <!--                <Home></Home>-->
              <!--              </el-tab-pane>-->
              <!--              <el-tab-pane label="代收列表" name="2">-->
              <!--                <PayLogList></PayLogList>-->
              <!--              </el-tab-pane>-->

            </el-tabs>
          </el-row>
          <!--                    <iframe id="iframe" :src="iframeSrc" ref="iframe"-->
          <!--                            style="width: 101%;height: 100%;overflow-x: hidden;overflow-y: hidden;border-style: none"></iframe>-->
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>


<script>
  import {getMenuList} from '@/api'
  import Home from './home'
  import PayLogList from "./logmanage/payLogList";
  import WithdrawLogList from "./logmanage/withdrawLogList";
  import OperaLogList from "./logmanage/operaLogList.vue";
  import MoneyLogList from "./logmanage/moneyLogList.vue";
  import CpInfoList from "./cpmanage/cpInfoList.vue";
  import AgentList from "./agentmanage/agentList.vue";
  import PayTypeList from "./typemanage/payTypeList.vue";
  import WithdrawTypeList from "./typemanage/withdrawTypeList.vue";
  import CpInfoTotalStatistics from "./statisticsmanage/cpInfoTotalStatistics.vue";
  import CpInfoDailyStatistics from "./statisticsmanage/cpInfoDailyStatistics.vue";
  import UserList from "./usermanage/userList.vue";
  import RoleList from "./rolemanage/roleList.vue";
  import ChannelTotalStatistics from "./statisticsmanage/channelTotalStatistics.vue";
  import ChannelDailyStatistics from "./statisticsmanage/channelDailyStatistics.vue";

  export default {
    el: "#app",
    name: "index",
    components: {
      Home,
      PayLogList,
      WithdrawLogList,
      OperaLogList,
      MoneyLogList,
      CpInfoList,
      AgentList,
      PayTypeList,
      WithdrawTypeList,
      CpInfoTotalStatistics,
      CpInfoDailyStatistics,
      UserList,
      RoleList,
      ChannelTotalStatistics,
      ChannelDailyStatistics

    },
    created() {
      this.editableTabs = []
      this.addTabs({id: 1, permissName: '首页', pValue: 'home'})
    },
    data() {
      return {
        fullscreenLoading: true,
        iframeSrc: './homeNew.html',
        isCollapse: false,
        iframe: [],
        clientHeight: 0,
        asideWidth: '12rem',
        tabsValues: '1',
        editableTabs: [],
        tabIndex: 2,
        heightClass: {'height': '937px'}
      }
    },
    watch: {
      // 如果 `clientHeight` 发生改变，这个函数就会运行
      clientHeight: function () {
        this.changeFixed(this.clientHeight)
      },
      editableTabs: {
        handler: function(newVal, oldVal) {
          if (newVal.length === 1 && newVal[0].name === 'home'){
            this.tabsValues = 'home'
          }
          // TO DO
        },
        deep: true,
        immediate: true
      }
    },
    mounted() {
      let that = this
      getMenuList().then(response => {
        if (response.data.code === 0) {
          that.iframe = response.data.data
        }
      })
      // 获取浏览器可视区域高度
      that.clientHeight = `${document.documentElement.clientHeight}`          //document.body.clientWidth;
      that.$refs.elmain.$el.style.height = that.clientHeight + 'px';
      that.$refs.elaside.$el.style.height = that.clientHeight + 'px';
      window.onresize = function temp() {
        that.clientHeight = `${document.documentElement.clientHeight}`;
      }
    },
    methods: {

      changeFixed(clientHeight) {                        //动态修改样式
        this.$refs['elaside'].$el.style.height = clientHeight + 'px';

        this.heightClass = {'height': clientHeight + 'px'}

      },
      ShowNavMenu() {
        this.isCollapse = !this.isCollapse
      },

      getIframeSrc(e) {
        this.iframeSrc = e
      },
      checkoutLogin() {
        top.location.href = "/login.jsp"
      },
      handleTabsEdit(targetName, action) {
        if (action === 'add') {
          let newTabName = ++this.tabIndex + '';
          this.editableTabs.push({
            title: 'New Tab',
            name: newTabName,
            content: ''
          });
          this.editableTabsValue = newTabName;
        }
        if (action === 'remove') {
          let tabs = this.editableTabs;
          let activeName = this.editableTabsValue;
          if (activeName === targetName) {
            tabs.forEach((tab, index) => {
              if (tab.name === targetName) {
                let nextTab = tabs[index + 1] || tabs[index - 1];
                if (nextTab) {
                  activeName = nextTab.name;
                }
              }
            });
          }

          this.editableTabsValue = activeName;
          this.editableTabs = tabs.filter(tab => tab.name !== targetName);
        }
      },
      addTabs(itemson) {
        const pType = itemson.pType
        const permissName = itemson.permissName
        const pValue = itemson.pValue

        let show = false;
        if (pValue != null) {
          var newTab = (title, name, content, show) => {
            return {
              title: title,
              name: name,
              content: content,
              show: show
            }
          }

          for (const tab of this.editableTabs) {
            if (tab.name === pValue) {
              this.tabsValues = pValue
              return
            }
          }
          this.editableTabs.push(newTab(permissName, pValue, pValue, true))
        }

        this.$nextTick(() => {
          this.tabsValues = pValue
        })
      },
      isClosable(item) {
        return item.name !== 'home';
      }

    }

  }
</script>

<style scoped>

  html, body, #app, .el-container {
    border-style: none;
    /*设置内部填充为0，几个布局元素之间没有间距*/
    padding: 0;
    /*外部间距也是如此设置*/
    margin: 0;
    /*统一设置高度为100%*/
    height: 100%;

  }

</style>
<style>
  * {
    margin: 0;
    padding: 0;
  }

  html {
    height: 100%;
  }

  body {
    height: 100%;
  }

  #app {
    height: 100%;
  }

  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }

  /* 侧边导航栏 */
  .el-row {
    max-width: 100%;
    /*height: 100%;*/
  }

  .el-col-12 {
    width: 100%;
    height: 100%;
  }

  .el-menu {
    width: 180px;
    border: none;
    background-color: #333;
    color: #ffffff;
  }

  .el-menu-item {
    color: #ffffff;
  }

  /* 侧边栏头部信息头像区域 */
  .top-info {
    display: flex;
    padding: 10px;
    height: 100px;
    box-sizing: border-box;
  }

  .top-info .top-info__image {

  }

  .top-info .top-info-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .top-info .top-info__image .el-image {
    width: 64px;
    height: 64px;
  }

  .el-submenu__title {
    color: #ffffff;
  }

  .el-menu-item.is-active {
    background-color: rgb(240, 62, 65) !important;
  }

  .shou {
    width: 64px !important;
  }

  /* 页面导航栏 */
  .page-nav-header {
    font-size: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .header__right {
    font-size: 14px;
    display: flex;
  }

  .header__right-info {
    color: #f03e41;
  }

  .el-menu-item:hover {
    color: #1787FB !important;
    /* text-decoration: underline; */
  }

  .el-submenu__title:hover {
    color: #1787FB !important;
  }

  .el-tabs__nav .el-tabs__item:nth-child(1) span {
    display: none;
  }

  .el-scrollbar__wrap {
    overflow-x: hidden;
  }
  .demo-table-expand {
    font-size: 0;
    margin-left: 60px;
  }
  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }
  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



