<template>
  <div id="app" style="width: 100%;height: 100%">
    <el-row type="flex" class="row-bg" justify="center">
      <el-col :span="6">
        <div class="grid-content bg-purple-light login-panel">
          <el-form ref="loginForm" :model="loginForm" label-width="80px">
            <el-form-item label="用户名">
              <el-input v-model="loginForm.userName"></el-input>
            </el-form-item>
            <el-form-item label="密码">
              <el-input type="password" v-model="loginForm.password"></el-input>
            </el-form-item>
            <el-form-item label="谷歌秘钥">
              <el-input v-model="loginForm.googleCode"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="login">登录</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import {login} from '../api/user'
  export default {
    el: "#app",
    name: "login",
    created() {
    },
    data() {
      return {
        loginForm: {
          userName: '',
          password: '',
          googleCode: ''

        }
      }
    },
    methods: {
      login() {
        const that = this;

        login(that.loginForm).then((resp) => {
          console.log(that.$router)
          if (resp.data.code === 0) {
            const data = resp.data.data
            if (data === 1) {
              that.$router.push('/index')
            }else if (data === -1){
              that.$message.error('账户不存在')
            }else if (data === -2){
              that.$message.error('账户未打开')
            }else if (data === -3){
              that.$message.error('密码错误')
            }else if (data === -4){
              that.$message.error('未填写谷歌验证码')
            }else if (data === -5){
              that.$message.error('谷歌验证码错误')
            }
          }


        })
      }
    }

  }
</script>

<style scoped>

  @import "../statics/index.css";


  .el-col {
    border-radius: 4px;
  }

  .bg-purple-dark {
    background: #99a9bf;
  }

  .bg-purple {
    background: #d3dce6;
  }

  .bg-purple-light {
    background: #e5e9f2;
  }

  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }

  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
</style>



